import React from 'react';
import './index.scss';
import i18n from 'i18n-js';
import { Link } from 'react-router-dom';
import { BreadCrumb } from 'primereact/breadcrumb';

const StreetNames = () => {
    const items = [
        { label: i18n.t("main") },
        { label: i18n.t("peace")},
        { label: i18n.t("streetnames-1") },
    ];

    const home = { icon: 'pi pi-home', url: '../' };

    return (
        <article className="page">
        <BreadCrumb model={items} home={home}/>
            <h1 className="page__header">
                {i18n.t("streetnames-1")}
            </h1>
            <p className="page__text">
                {i18n.t("streetnames-2")}
            </p>
            <p className="page__text">
                {i18n.t("streetnames-3")}
            </p>
            <p className="page__text">
                {i18n.t("streetnames-4")}
                <Link to="/main/donbasspeace" className="page__link" onClick={() => { window.scrollTo({ top: 0, behavior: "smooth" }) }}>
                    {i18n.t("streetnames-5")}
                </Link>
            </p>
        </article>
    );
}

export default StreetNames;